import { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';

import AuthInput from '../../components/Auth/AuthInput/AuthInput.jsx'
import AuthPageContainer from '../../components/Auth/AuthPageContainer/AuthPageContainer.jsx'
import Button from '../../components/Button/Button.jsx'
import {Toast} from '../../utility/helper.js'
import {userLogin} from '../../api/user.js'

import styles from './LoginPage.module.scss'

export default function LoginPage () {
  const [account, setAccount] = useState("");
  const [password, setPassword] = useState("");
  const navigate = useNavigate();

  const handleClick = async () => {
    if (account.trim().length === 0) {
      Toast.fire({
        title: "!",
        icon: "error",
      });
      return;
    }
    if (password.trim().length === 0) {
      Toast.fire({
        title: "!",
        icon: "error",
      });
      return;
    }
    const data = await userLogin({
      account,
      password,
    });
    // login success
    if (data.success) {
      localStorage.setItem("token", data.data.token);
      Toast.fire({
        title: "",
        icon: "success",
      });
      navigate("/main");
      return;
    // login fail
    } 
    Toast.fire({
        title: "",
        icon: "error",
      });
  }

  return (
    <AuthPageContainer title=' Alphitter'>
        <AuthInput label='' value={account} placeholder='' onChange={(accountInputValue) => setAccount(accountInputValue)}
        notification='!' wordsLimit={20}
        />
        <AuthInput label='' value={password} placeholder='' onChange={(passwordInputValue) => setPassword(passwordInputValue)}
        notification='!' wordsLimit={20} type='password'
        />
        <Button title='' size='large' isAction onClick={handleClick}></Button>
        <div className={styles.link}>
          <Link to="/signup">
            <span className={styles.span}></span>
          </Link>
          <span></span>
          <Link to="/admin/login">
            <span className={styles.span}></span>
          </Link>
        </div>
    </AuthPageContainer>
  );
}